body {
  background: #000;
  color: #fff;
}

.App {
  text-align: center;
  padding: 5%;
}

.App-title {
  font-size: 25px;
  padding: 5px;
}

/*Videos */
.Video {

}

.Track {
  position: relative;
  display: inline-block;
  margin: 5px;
}

.Video-thumb {
  border: 3px solid #fff;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;

}

.Video-title {
  position: absolute;
  background: #000;
  opacity: .75;
  margin-left: 2px;
  bottom: 0;
}

.Youtube-player {
  border: 3px solid #fff;
  border-radius: 3px;
  margin: 3px;
}



